import React from "react"
import img1 from "../images/gallery/img1.png"
import img2 from "../images/gallery/img2.png"
import img3 from "../images/gallery/img3.png"
import img5 from "../images/gallery/img5.png"
import testimony1 from "../images/../images/testimony/0001.png"
import testimony2 from "../images/../images/testimony/002.png"
import testimony3 from "../images/../images/testimony/0003.png"
import testimony4 from "../images/../images/testimony/004.png"
import testimony5 from "../images/../images/testimony/005.png"
import testimony6 from "../images/../images/testimony/006.png"
import "materialize-css/dist/css/materialize.min.css";
import "../styles/global.css"
import Nav from "../components/navigation/Nav"

class IndexPage extends React.Component {

  M;
  componentDidMount() {
    const M = require("materialize-css/dist/js/materialize.min.js");
    this.M = M;
    let elems = document.querySelectorAll(".carousel");
    this.M.Modal.init(elems)
    var signModal = document.querySelectorAll(".modal");
    this.M.Carousel.init(elems, {
      fullWidth: true,
      numVisible: 1,
      indicators: true
    })
  }
  render() {

    return (
      <>
        <Nav />
        <div className="container">
          <div className="carousel carousel-slider slideshow" >
            <a href="#one!" className="carousel-item" style={{ minHeight: "250px", maxHeight: "250px" }}><img src={img1} alt="carousel imgage" /></a>
            <a href="#two!" className="carousel-item" style={{ maxHeight: "250px" }}><img src={img2} alt="carousel imgage" /></a>
            <a href="#three!" className="carousel-item" style={{ maxHeight: "250px" }}><img src={img3} alt="carousel imgage" /></a>
            {/* <a href="#four!" className="carousel-item" style={{ maxHeight: "250px" }}><img src={img4} alt="carousel imgage" /></a> */}
            <a href="#five!" className="carousel-item" style={{ maxHeight: "250px" }}><img src={img5} alt="carousel imgage" /></a>
          </div>
          <Testimonies />
        </div>
      </>
    )
  }

}


class Testimonies extends React.Component {
  render() {
    return (
      <>
        <h4 className="center-align">Testimonies from our amiable parents</h4>
        <div className="row">
          <div className="col s12 m4">
            <div className="card">
              <div className="card-image">
                <img src={testimony1} alt="img" />
              </div>
              <div className="card-content">
                <span className="card-title">Mrs Udin Utit Uko</span>
                <p>Ebenezer Bells Academy is a fabulous school that has changed my children for the better; Academically, spiritually and morally</p>
              </div>
            </div>
          </div>

          <div className="col s12 m4">
            <div className="card">
              <div className="card-image">
                <img src={testimony2} alt="img" />
              </div>
              <div className="card-content">
                <span className="card-title">Mr Louis Okim Ikpan</span>
                <p>Ebenezer Bells Academy is a school which excels academically. i also commend them on children welfare, discipline and their extra mural activities</p>
              </div>
            </div>
          </div>

          <div className="col s12 m4">
            <div className="card">
              <div className="card-image">
                <img src={testimony3} alt="img" />
              </div>
              <div className="card-content">
                <span className="card-title">Mrs Naomi Sunday</span>
                <p>My children are academically sound and intelligent. their command of english is also excellent and i will commend the school to anyone. you won't regreet it</p>
              </div>
            </div>
          </div>

          <div className="col s12 m4">
            <div className="card">
              <div className="card-image">
                <img src={testimony5} alt="img" />
              </div>
              <div className="card-content">
                <span className="card-title">Mrs Afolabi Omabuwa</span>
                <p>Ebenezer Bells Academy is one of the best schools in Cross River State. my children stand out where ever we go and i dont mind driving long distaces to send my children to school</p>
              </div>
            </div>
          </div>

          <div className="col s12 m4">
            <div className="card">
              <div className="card-image">
                <img src={testimony4} alt="img" />
              </div>
              <div className="card-content">
                <span className="card-title">Mrs Effa Etim Egede</span>
                <p>I want to commend Ebenezer Bells Academy that even in the midst of this pandemic still figured out a way to coach the children from home</p>
              </div>
            </div>
          </div>

          <div className="col s12 m4">
            <div className="card">
              <div className="card-image">
                <img src={testimony6} alt="img" />
              </div>
              <div className="card-content">
                <span className="card-title">Mrs Eunice Ofem</span>
                <p>I am grateful to Ebenezer Bells Academy for impacting knowledge into my kids who at the age of 3+ were able to join and read three letters words and at 4 were reading fluently</p>
              </div>
            </div>
          </div>

        </div>
      </>
    );
  }
}

export default IndexPage